
.App {
  text-align: center;
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
  z-index: -100;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button {
    margin: auto;
    background-color: #2b712d; /* Green */
    border: none;
    color: rgb(blue);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }

  .addressNbutton {
    margin: auto;
    display:block;
    width: 100%;
    text-align: center;
    border: 3px solid rgb(16, 21, 91);
    padding: 10px;
  }

  .logoBox {
    margin: auto;
    display:block;
    width: 100%;
    text-align: center;
